import React, { Component } from 'react'
import Inptut from './Input'
import List from './List'
export default class TodoList extends Component {
    constructor(){
        super();
        this.state = {
            inpValue : '',
            todos:[
                {title:'1',done:false},
                {title:'2',done:true},
                {title:'3',done:true},
                {title:'4',done:false}
            ]
        }
    }

    componentDidMount(){
        
        let todos = localStorage.getItem('todos');
        if(todos){
            console.log(todos);
            this.setState({
                
                todos:JSON.parse(todos)
            })
        }
    }
    componentDidUpdate(){
        localStorage.setItem(
            'todos',
            JSON.stringify(this.state.todos)
        )
    }
    handleChang = (e)=>{
        this.setState({inpValue:e.target.value})
    }

    addTodo = ()=>{
        if(this.state.inpValue!=""){
            this.setState({todos: [
                        {
                            title:this.state.inpValue, done:false
                        },
                        ...this.state.todos
                        
                    ]
                    
            })
            this.setState(
                {inpValue:""}
            )
        }
        else{
            alert("输入不能为空");
        }
    }

    delTodo = (idx)=>{
        // let todos = this.props.todos;
        this.setState({
            todos:this.state.todos.filter((item,index)=>index != idx )
        });
    }
    toggle = (index)=>{
        let newTodos =JSON.parse(JSON.stringify(this.state.todos)) 

        newTodos[index].done =!newTodos[index].done
        this.setState({
            todos:newTodos
        })
    }
    render() {
        
        return (
            <div>
                <Inptut inpValue = {this.state.inpValue}
                todos = {this.state.todos}
                add = {this.addTodo}
                hand = {this.handleChang}/>
                <List dele = {this.delTodo}
                tog = {this.toggle} 
                todos = {this.state.todos}
                />
            </div>
        )
    }
}
