import React, { Component } from 'react'

export default class List extends Component {

   

    // delTodo = (idx)=>{
    //     let todos = this.props.todos;
    //     this.setState({
    //         todos:todos.filter((item,index)=>index != idx )
    //     });
    // }
    delTodo = (index)=>{
        this.props.dele(index)
    }
    // toggle = (index)=>{
    //     let todos = this.props.todos;
    //     let newTodos =JSON.parse(JSON.stringify(todos)) 

    //     newTodos[index].done =!newTodos[index].done
    //     this.setState({
    //         todos:newTodos
    //     })
    // }
    toggle = (index)=>{
        this.props.tog(index);
    }
    renderTodos = (done)=>{
        let todos = this.props.todos;
        return todos.map((todo,idx)=>{
            if(done==todo.done){
                return<li>
                    <input onClick={()=>this.toggle(idx)}
                    checked={done} type="checkbox" />
                    <span dangerouslySetInnerHTML={{__html:todo.title}}></span>
                    <button 
                        onClick = {()=>this.delTodo(idx)}
                    >
                        删除
                    </button>

                </li>
            }
            
            
        })
    }

    render() {
        let todos = this.props.todos;
        let arr1 = todos.filter(item=>
            !item.done
        );
        
        return (
            
            <div>
                <h2>正在进行{arr1.length}</h2>
                <ul>
                    {
                        this.renderTodos(false)
                    }
                </ul>
                <h2>已经完成</h2>
                <ul>
                    {
                        this.renderTodos(true)
                    }
                </ul>
            </div>
        )
    }
}

